<template>
    <div id="wrapper">
        <navbar id="nav" class="no-print animate__animated animate__fadeInLeft"  style="display: none;"/>
        <div class="d-flex flex-column animate__animated animate__fadeInRight" id="content-wrapper" style="display: none !important;">
            <div id="content">
                <header-btn class="no-print"/>
                <div class="container-fluid text-dark">
                    <div class="row px-1 py-3 mb-1 no-print">
                        <label class="mt-1 col-xl-1 mb-2"> From: </label>
                        <input class="form-control col-xl-2 col-md-4 mb-2" v-model="from" type="number">

                        <label class="mt-1 col-xl-1 mb-2"> To: </label>
                        <input type="number" class="form-control col-xl-2 col-md-4 mb-2" v-model="to">
                        
                        <label class="col-sm-1 mt-1"> Staffs: </label>
                        <select v-model="staff_id" class="form-control col-sm-3 mb-2">
                            <option value=""></option>
                            <option v-for="staff in staffs" :key="staff.st_id" v-bind:value="staff.st_id"> {{ staff.staff_name }} </option>
                        </select>

                        <button class="btn btn-success col-xl-2 col-md-4 mx-0 mb-2" v-on:click="search()"> Search <i class="fa fa-search"></i> </button><hr>
                    </div>
                    <hr class="no-print">
                    <div class="row no-print" dir="rtl">
                        <label class="mt-1 col-xl-1 mb-2"> : </label>
                        <input class="form-control col-xl-2 col-md-4 mb-2" v-model="manager" type="text" @change="changeNames()">

                        <label class="mt-1 col-xl-1 mb-2"> : </label>
                        <input type="text" class="form-control col-xl-2 col-md-4 mb-2" v-model="accountant" @change="changeNames()">

                        <label class="mt-1 col-xl-1 mb-2"> : </label>
                        <input type="text" class="form-control col-xl-2 col-md-4 mb-2" v-model="kargeri" @change="changeNames()">
                    </div>
                    <hr class="no-print">
                    <div class="row mt-3">
                        <div class="col-4" style="color: black;">
                            <h3> Ideal Power Company </h3>
                            <p> For Contracting Electrical, Mechanical,                                                                                                             General Trading, Renewable Energy
                             &#38; Clean Energy / LTD
                            </p>
                        </div>
                        <div class="col-4 text-center" style="color: black;">
                            <img src="../assets/Ideal power logo.png" width="75%" class="mt-n3">
                        </div>
                        <div class="col-4 text-right" style="color: black;">
                            <h2>    </h2>
                            <p>                / 
                            </p>
                        </div>
                    </div>
                    <hr class="my-1">
                    <div> {{ new Date().toISOString().split('T')[0] }} </div>
                    <div style="color: black;" class="mt-1">
                        <p class="text-center">   </p>
                        <p class="text-right mt-5" dir="rtl">
                                ({{ (total).toLocaleString() }}) {{ numToWord(total) }}    {{ staff_id ? staffs.find(obj => obj.st_id == staff_id).staff_name : '' }}     {{ from }}   {{ to }}     :
                        </p>
                    </div>
                    <div>
                        <table dir="rtl" class="table table-bordered table-striped text-center" style="page-break-inside: always; font-size: 12pt !important;">
                            <thead>
                                <th style="width: 10%;">  </th>
                                <th style="width: 10%;">  </th>
                                <th style="width: 9%;">  </th>
                                <th style="width: 9%;">  </th>
                                <th style="width: 13%;">   </th>
                                <th>  </th>
                            </thead>
                            <tbody>
                                <tr v-for="item in expenses" :key="item.staff_expense_id">
                                    <td> {{ item.form }} </td>
                                    <td> {{ new Date(item.expense_date).toISOString().split('T')[0] }} </td>
                                    <td> {{ item.qty }} </td>
                                    <td> {{ (item.price).toLocaleString() }} </td>
                                    <td> {{ (item.total).toLocaleString() }} </td>
                                    <td> {{ item.expense_text }} </td>
                                </tr>
                                <tr>
                                    <td colspan="4"  class="text-left para-total"> : </td>
                                    <td class="para-total"> {{ (total).toLocaleString() }} </td>
                                    <td class="para-total"> {{ numToWord(total) }}  </td>
                                </tr>
                            </tbody>
                        </table>
                        <div class="row mt-15 mb-8" dir="rtl">
                            <div class="col-4 text-center" style="color: black;">
                                
                                <br>
                                {{ manager }}
                            </div>
                            <div class="col-4 text-center" style="color: black;">
                                
                                <br>
                                {{ accountant }}
                            </div>
                            <div class="col-4 text-center" style="color: black;">
                                 
                                <br>
                                {{ kargeri }}
                            </div>
                        </div>
                        <img src="../assets/footer.png" alt="" width="100%">
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import auth from '../auth';
import KurdishSoraniNumbersToWords from 'kurdish-numbers-to-words/Sorani.js'
export default {
    name: "staff_expenses_report",
    data() {
        return {
            staff_id: null,
            staffs: [],
            expenses: [],
            month: new Date().getMonth() + 1,
            year: new Date().getFullYear(),
            from: 1,
            to: 1,
            employees: [],
            manager: '',
            accountant: '',
            kargeri: ''
        }
    },
    beforeCreate() {
        auth("staff_expenses_report");
    },
    created() {
        this.$http.get('/getNames').then(({data}) => {
            this.manager = data.manager;
            this.kargeri = data.kargeri;
            this.accountant = data.accountant;
        });
        this.$http.post('/staff/getData').then(({data})=>{
            this.staffs = data.filter(obj => obj.show_staff == '1');
        })

    },
    methods: {
        numToWord(num) {
            return KurdishSoraniNumbersToWords.convert(num);
        },
        search() {
            if(this.from && this.to && this.staff_id) {
                this.$http.get('/staff_expenses/getDataByStaff/' + this.staff_id + '/' + this.from + '/' + this.to).then(({data}) => {
                    this.expenses = data;
                })
            } else {
                this.expenses = [];
            }
        },
        changeNames() {
            this.$http.post('/setNames', {
                manager: this.manager,
                kargeri: this.kargeri,
                accountant: this.accountant
            }).then(() => {
                
            })
        }
    },
    computed: {
        total() {
            return this.expenses.reduce((a, b) => a + b.total, 0);
        }
    },
    watch: {
        staff_id() {
            this.search();
        },
        from() {
            this.search();
        },
        to() {
            this.search();
        },
    }
}
</script>

<style scoped>
    .rtl {
        text-align: right !important;
    }
    tbody td {
        padding: .5rem;
    }
    .para {
        background-color: rgb(81, 224, 81) !important;
        color: black !important;
    }
    .para-total {
        background-color: rgb(68, 171, 231) !important;
        color: black !important;
    }
    th, td {
        font-size: 12pt;
        color: black;
        font-weight: 500;
    }
    @media print {
        .no-print, #nav {
            display: none;
        }
        *:not(.para, .para-total, .expired-passport-date) {
            background-color: white ;
                
        }
        .para {
            background-color: rgb(81, 224, 81) !important;
            color: black !important;
        }
        .para-total {
            background-color: rgb(68, 171, 231) !important;
            color: black !important;
        }
        table {
            font-size: 9pt !important;
        }
        @page {
            size: landscape !important;
        }
    }
</style>